.card {
  border: 1px solid #f3f3f3;
  padding: 20px;
  width: 220px;
  border-radius: 40px;
  margin-right: 30px;
  margin-bottom: 30px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.plus {
  cursor: pointer;
}

.card:hover {
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.06);
  transform: translateY(-5px);
}

.favorite {
  position: absolute;
  cursor: pointer;
}

.cardDescription {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cardPrice {
  display: flex;
  flex-direction: column;
}

.cardPrice span {
  opacity: 0.5;
  text-transform: uppercase;
  margin: 0;
  font-size: 13px;
}

.cardPrice b {
  font-size: 14px;
}

.card h5 {
  font-weight: 400;
  font-size: 14px;
}