body {
  font-family: "Inter", system-ui;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f7fbfe;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}
* {
  font-family: "Inter", system-ui;
}
a {
  text-decoration: none;
  color: inherit;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}

.wrapper {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}


.cartEmpty {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  flex: wrap;
  padding-top: 100px;
}
.cartEmptyImg {
  width: 120px;
  height: 120px;
  margin-bottom: 20px;
}
.cartEmptyImg:hover {
  transform: rotate(180deg) translateX(3p);
}

.cartEmpty p {
  width: 280px;
  line-height: 24px;
  margin-bottom: 30px;
  opacity: 0.6;
}
.cartEmpty h2 {
  margin-bottom: 10px;
}
.greenButtonTop {
  width: 245px;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
.greenButtonTop img {
  position: relative;
  top: 1px;
  margin-right: 15px;
  transition: transform 0.15s ease-in-out;
}


.cartItem {
  display: flex;
  align-items: center;
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;
  margin-bottom: 20px;
}
.cartItemImg {
  height: 70px;
  width: 70px;
  background-size: contain;
  background-position: 0 10px;
  background-repeat: no-repeat;
  margin-right: 20px;
}
.cartItem img {
  margin-right: 20px;
}
.cartItem p {
  margin-bottom: 5px;
}
.cartPrice {
  margin-right: 20px;
}
.cartPrice p {
  font-size: 16px;
  margin: 0;
  margin-bottom: 5px;
}
.cartPrice b {
  font-size: 14px;
}
.cartTotalBlock ul {
  display: block;
  margin-bottom: 40px !important;
}
.cartTotalBlock li {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
}
.cartTotalBlock div {
  flex: 1;
  height: 1px;
  border-bottom: 1px dashed #dfdfdf;
  position: relative;
  top: -4px;
  margin: 0 7px;
}
.cartTotalBlock .greenButton {
  position: relative;
}
.cartTotalBlock .greenButton img {
  position: absolute;
  right: 30px;
  top: 20px;
  transition: transform 0.2s ease-in-out;
  transform: rotate(180deg);
}
.cartTotalBlock .greenButton img:hover {
  transform: translateX(10px);
}
.greenButton {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.5s ease-in-out;
}
.greenButton:disabled {
  background-color: #bebebe !important;
  cursor: default; 
  animation: button-loading 0.9s ease-in-out infinite;
}
.greenButton:hover {
  background: lighten(#9dd558, 5%);
}
.greenButton:active {
  background: darken(#9dd558, 5%);
}

.closeBtn {
  cursor: pointer;
}
.removeBtn {
  position: relative;
  right: 0;
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 0.15 ease-in-out;
  width: 18px;
  height: 18px;
  top: 14px;
  right: 15px;
}
.removeBtn:hover {
  opacity: 1;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px;
  border-bottom: 1px solid #eaeaea;
}
.headerLeft {
  display: flex;
  align-items: center;
}
.headerLeft h3 {
  text-transform: uppercase;
  margin: 0;
}
.headerLeft p {
  margin: 0;
  opacity: 0.5;
}
.headerLeft img {
  margin-right: 15px;
}
.headerRight {
  display: flex;
}
.headerRight li {
  cursor: pointer;
  margin-left: 30px;
}

span {
  margin-left: 10px;
  margin-right: 30px;
}
.content {
  padding: 40px;
}
.contentInput {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
.content h1 {
  margin: 0;
}
.search-block {
  position: relative;
  display: flex;
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
}
.inputSearch {
  width: 200px;
  border: 0px;
  padding: 13px;
  font-size: 16px;
}
.sneakers {
  display: flex;
  flex-wrap: wrap;
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}